import React, {Component} from "react";
import WWTForm from "eshop/modules/magnum2/components/wwt/WWTForm";
import {isPeselAndAddressVerified} from "../actions/api";

export default class AddresSectionComponent extends React.Component {
    constructor(props) {
        super(props);
    }


    verifyCustomerSearched(cityId, streetId, streetNo, appartmentNo, cityName, streetName) {
        this.props.requestPeselAndAddressAuth({
            townId: cityId,
            streetId: streetId,
            streetNumber: streetNo,
            appartmentNo: appartmentNo,
            town: cityName,
            streetName: streetName
        }, this.props.msisdn,
            this.props.descriptions&&this.props.descriptions.peselAndAddressVerificationTimeout||30000);
    }

    render() {
        return (
            <div>
                <WWTForm {...this.props}
                         wwt={{}}
                         isModal={true}
                         isNarrow={true}
                         focusCityOnMount={true}
                         onSubmit={this.verifyCustomerSearched.bind(this)}/>
            </div>
        );
    }
}